body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


a {
  color: whitesmoke;
}

#videobackground .gradient {
  background: linear-gradient(to bottom right, rgb(0, 105, 209) 0%, rgb(255, 255, 255) 92.8%);
}

#videobackground .view video {
  top: 0!important;
  left: 0!important;
  transform: none!important;
  height: 100vh;
}

.welcome {
  font-weight: bolder;
  font-size: 70px;
}

.secondWelcome {
  font-weight: bolder;
  margin-bottom: 40px;
}

.headers {
  font-family: 'Ubuntu', sans-serif;
  color: whitesmoke;
  font-weight: 500;
  font-size: 20px;
}

.sub-content {
  font-family: 'Quicksand', sans-serif;
  font-size: 20px;
  font-weight: 400;
  color: black;
}

.table-content {
  margin-bottom: 10px;
}

.budget-content {
  font-family: 'Quicksand', sans-serif;
  font-size: 20px;
  font-weight: 400;
}

#navStyle {
  color: white;
  font-weight: bolder;
  font-size: 20px;
  border-right: 2px solid whitesmoke;
  padding: 5px;
}

#navStyleP {
  color: white;
  font-weight: bolder;
  font-size: 20px;
  padding: 5px;
}

#table {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.my-subs {
  font-family: 'Noto Sans', sans-serif;
  color: black;
  font-weight: 700;
}

.budget {
  font-family: 'Ubuntu', sans-serif;
  color: whitesmoke;
  font-weight: 500;
  font-size: 20px;
}

.icons {
  font-size: 30px;
}

.sub-icons {
  color: black;
}

.members-stuff {
  margin-top: 150px;
}

.sub-btn {
  width: 50%;
}

.due-date {
  margin-left: 0px;
}

@media only screen and (max-width: 800px) {
  p {
    font-size: 60%;
  }
  .home-content {
    margin-top: 40%;
  }
  .sub-btn {
    height: 30%;
    width: 30%;
    position: relative;
  }
  .icons {
    font-size: 20px;
  }
  .sub-icons {
    position: absolute;
    bottom: 1px;
    right: 3px;
  }
  .budget-content {
    font-size: 15px;
  }
  .budget {
    font-size: 15px;
  }
  .members-stuff {
    margin-top: 70px;
  }
  .price {
    padding-left: 10px;
  }
  .due-date {
    margin-left: 10px;
  }
  .start-date {
    margin-left: -10px;
  }
  .my-subs {
    font-size: 25px;
  }
}
.lol {
    background: black;
    background: linear-gradient(to bottom right, rgb(0, 105, 209) 0%, rgb(255, 255, 255) 92.8%);
    position: absolute;
    left: 0;
    width: 100%;
    height: auto;
    min-height: 100%;
    overflow: hidden;
}
.container {
    position: relative;
    z-index: 2;
}
.bg-bubbles {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
}
.bg-bubbles li {
    position: absolute;
    list-style: none;
    display: block;
    width: 40px;
    height: 40px;
    background-color: rgba(255, 255, 255, 0.15);
    bottom: -160px;
    animation: square 25s infinite;
    transition-timing-function: linear;
}
.bg-bubbles li:nth-child(1) {
    left: 10%;
}
.bg-bubbles li:nth-child(2) {
    left: 20%;
    width: 80px;
    height: 80px;
    animation-delay: 2s;
    animation-duration: 17s;
}
.bg-bubbles li:nth-child(3) {
    left: 25%;
    animation-delay: 4s;
}
.bg-bubbles li:nth-child(4) {
    left: 40%;
    width: 60px;
    height: 60px;
    animation-duration: 22s;
    background-color: rgba(255, 255, 255, 0.25);
}
.bg-bubbles li:nth-child(5) {
    left: 70%;
}
.bg-bubbles li:nth-child(6) {
    left: 80%;
    width: 120px;
    height: 120px;
    animation-delay: 3s;
    background-color: rgba(255, 255, 255, 0.2);
}
.bg-bubbles li:nth-child(7) {
    left: 32%;
    width: 160px;
    height: 160px;
    animation-delay: 7s;
}
.bg-bubbles li:nth-child(8) {
    left: 55%;
    width: 20px;
    height: 20px;
    animation-delay: 15s;
    animation-duration: 40s;
}
.bg-bubbles li:nth-child(9) {
    left: 25%;
    width: 10px;
    height: 10px;
    animation-delay: 2s;
    animation-duration: 40s;
    background-color: rgba(255, 255, 255, 0.3);
}
.bg-bubbles li:nth-child(10) {
    left: 90%;
    width: 160px;
    height: 160px;
    animation-delay: 11s;
}
@keyframes square {
    0% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(-700px) rotate(600deg);
    }
}
